* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

[data-start], [data-stop] {
  width: 145px;
  height: 40px;
  letter-spacing: .04em;
  text-align: center;
  color: #3b3b3b;
  cursor: pointer;
  background-color: rgba(128, 203, 196, .6);
  border: 1px solid #3b3b3b;
  border-radius: 5px;
  font-size: 18px;
  transition: all .25s;
}

[data-start]:hover, [data-stop]:hover {
  background-color: #80cbc4;
}

[data-start]:focus, [data-stop]:focus {
  background-color: #80cbc4;
  border: 3px solid #fff;
  outline: 3px solid #80cbc4;
}

[data-start]:disabled, [data-stop]:disabled {
  color: rgba(0, 0, 0, .3);
  opacity: .8;
  cursor: not-allowed;
  background-color: rgba(59, 59, 59, .3);
  border: 1px solid rgba(0, 0, 0, .3);
}

